import mobileBankingIcon from './mobile-banking.png';
import counterServiceIcon from './counter-service.png';
import sevenElevenIcon from './7-eleven.png';
import creditCardIcon from './credit-card.png';
import linePayIcon from './line-pay.png';
import trueWalletIcon from './true-wallet.png';
import TBankIcon from './t-bank.png';
import KBankIcon from './k-bank.png';
import SCBIcon from './scb.png';

const icons = {
    'mobile-banking': mobileBankingIcon,
    'counter-service': counterServiceIcon,
    '7-eleven': sevenElevenIcon,
    'line-pay': linePayIcon,
    'true-wallet': trueWalletIcon,
    'credit-card': creditCardIcon,
    't-bank': TBankIcon,
    'k-bank': KBankIcon,
    'scb': SCBIcon
}

const getIcon = (name) => {
    return icons[name];
}

export default getIcon;